		 * 点击后，会收起弹窗
		 */
		condIsClick(e) {
			if (e == 0) {
				this.condNum = 0;
				this.isCondScreen = false;
			} else if (e == 1) {
				this.condNum = 1;
				this.isCondScreen = false;
			} else if (e == 2) {
				this.condNum = 2;
				this.isCondScreen = false;
			} else {
				this.condNum = null;
			}
		},

