			// 更新地图标注的数据
			this.covers = this.getSceneData().filter((e) => {
				return e.name == targetName;
			});
			// 更新列表展示的数据
			this.listTarget = this.getListTarget().filter((e) => {
				return e.firLabels.includes(targetName)
			})

			this.makerIsTap = false; // 收起商家详情弹窗
